.widgets{
    position: sticky;
    top: 80px;
    flex:0.2;
    background-color: white;
    border-radius: 10px;
    border: 1px solid lightgray;
     padding-bottom: 10px;
     height: fit-content;
}
.widgets_header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;

}
.widgets_header > h2{
    font-size: 16px;
    font-weight: 600;
}
.widget_article {
    display: flex;
    padding: 30px;
    cursor: pointer;
}
.widget_article  :hover{
    background-color: whitesmoke;
}
.widgets_articleleft{
    color: #0177b7;
    margin-right: 5px;
}
.widgets_articleleft >  .GoPrimitiveDot-root {
      font-size: 15px;
}
.widgets_articleright{
    flex:1;
}
.widgets_articleright > p{
    font-size: 12px;
    color: gray;
}